    <main-part>
      <search-box :searchData="searchData" :table="table"></search-box>
      <el-table :data="table.list" class="el-table" border style="width: 100%">

        <el-table-column align="center" fixed label="姓名" width="100">
          <template slot-scope="scope">
            <p v-if="scope.row.nameChange">原:{{scope.row.nameChange.origValue}}</p>
            <p v-if="scope.row.nameChange">现:{{scope.row.nameChange.newValue}}</p>
            <el-button type="text"
                       size="medium"
                       v-if="scope.row.auditStatus === 'UNAUDITED' && !scope.row.nameChange"
                       @click="showDialog(scope.row,'edit')"
            >{{scope.row.studentName}}</el-button>
            <el-button type="text"
                       size="medium"
                       v-if="scope.row.auditStatus !== 'UNAUDITED' && !scope.row.nameChange"
                       @click="showDialog(scope.row,'view')"
            >{{scope.row.studentName}}</el-button>
            <!--<p v-if="!scope.row.nameChange">{{scope.row.studentName}}</p>-->
          </template>
        </el-table-column>
        <el-table-column label="身份证号" width="200">
          <template slot-scope="scope">
            <p v-if="scope.row.idCardChange">原:{{scope.row.idCardChange.origValue}}</p>
            <p v-if="scope.row.idCardChange">现:{{scope.row.idCardChange.newValue}}</p>
            <p v-if="!scope.row.idCardChange">--</p>
          </template>
        </el-table-column>
        <el-table-column label="政治面貌" width="100">
          <template slot-scope="scope">
            <p v-if="scope.row.politicsChange">原:{{scope.row.politicsChange.origValue}}</p>
            <p v-if="scope.row.politicsChange">现:{{scope.row.politicsChange.newValue}}</p>
            <p v-if="!scope.row.politicsChange">--</p>
          </template>
        </el-table-column>
        <el-table-column label="现居住地址" width="240">
          <template slot-scope="scope">
            <p v-if="scope.row.addressChange">原:{{scope.row.addressChange.origValue}}</p>
            <p v-if="scope.row.addressChange">现:{{scope.row.addressChange.newValue}}</p>
            <p v-if="!scope.row.addressChange">--</p>
          </template>
        </el-table-column>
        <el-table-column label="父亲信息" width="240">
          <template slot-scope="scope">
            <p v-if="scope.row.fatherChange">原:{{scope.row.fatherChange.origValue}}</p>
            <p v-if="scope.row.fatherChange">现:{{scope.row.fatherChange.newValue}}</p>
            <p v-if="!scope.row.fatherChange">--</p>
          </template>
        </el-table-column>
        <el-table-column label="母亲信息" width="240">
          <template slot-scope="scope">
            <p v-if="scope.row.motherChange">原:{{scope.row.motherChange.origValue}}</p>
            <p v-if="scope.row.motherChange">现:{{scope.row.motherChange.newValue}}</p>
            <p v-if="!scope.row.motherChange">--</p>
          </template>
        </el-table-column>
        <el-table-column label="监护人信息" width="240">
          <template slot-scope="scope">
            <p v-if="scope.row.guardianChange">原:{{scope.row.guardianChange.origValue}}</p>
            <p v-if="scope.row.guardianChange">现:{{scope.row.guardianChange.newValue}}</p>
            <p v-if="!scope.row.guardianChange">--</p>
          </template>
        </el-table-column>
        <el-table-column fixed="right" align="center" label="提交时间" width="100">
          <template slot-scope="scope">
            <p>{{scope.row.auditTime | substrTime}}</p>
          </template>
        </el-table-column>
        <el-table-column fixed="right" align="center" label="状态" width="80">
          <template slot-scope="scope">
            <p>{{$enums.AuditStatus.getName(scope.row.auditStatus)}}</p>
          </template>
        </el-table-column>
        <el-table-column fixed="right" align="center" label="操作" width="80">
          <template slot-scope="scope">
            <el-button type="text"
                       size="medium"
                       v-if="scope.row.auditStatus === 'UNAUDITED'"
                       @click="showDialog(scope.row,'edit')"
            >编辑</el-button>
            <el-button type="text"
                       size="medium"
                       v-if="scope.row.auditStatus !== 'UNAUDITED'"
                       @click="showDialog(scope.row,'view')"
            >查看</el-button>
          </template>
        </el-table-column>
      </el-table>
      <no-data v-show="!table.noData"></no-data>
      <paging :data.sync="table"></paging>
    </main-part>
    <el-dialog width="550px" :visible.sync="dialog" class="dialog" :title="form.view ? '查看': '编辑'">
      <div class="item">
        <span class="imp title">审核结果</span>
        <el-select :disabled="form.view" default-first-option v-model="form.auditStatus" class="input" filterable clearable>
          <el-option v-if="item.value !== 'UNAUDITED'" v-for="(item,key) in $enums.AuditStatus.list"
            :label="item.label" :value="item.value" :key="key"
          ></el-option>
        </el-select>
      </div>
      <div class="item" v-show="form.auditStatus === 'NOT_PASS'">
        <p class="long-title mb10 imp">未通过原因</p>
        <el-input :disabled="form.view" v-model="form.remark" :rows="3" type="textarea" placeholder="请输入原因"></el-input>
      </div>

      <div class="item" v-show="form.auditStatus === 'PART_PASS' && !form.view">
        <p class="long-title imp">请选择通过项</p>
        <el-checkbox-group class="radio-group" v-model="form.changeIdList">
          <el-checkbox :key="key" class="radio"
                    v-for="(item,key) in form.studentChangeList"
                    :label="item.id" border>{{$enums.ChangeType.getName(item.changeType)}}</el-checkbox>
        </el-checkbox-group>
      </div>

      <div class="item" v-show="form.auditStatus === 'PART_PASS' && form.view">
        <p class="long-title imp">请选择通过项</p>
        <el-checkbox-group disabled class="radio-group" v-model="form.changeIdList">
          <el-checkbox :key="key" class="radio"
                       v-for="(item,key) in $enums.ChangeType.list"
                       :label="item.value" border>{{item.label}}</el-checkbox>
        </el-checkbox-group>
      </div>

      <div class="item" v-show="form.auditStatus === 'PART_PASS'">
        <p class="long-title mb10 imp">剩余项未通过原因</p>
        <el-input :disabled="form.view" v-model="form.remark" :rows="3" type="textarea" placeholder="请输入原因"></el-input>
      </div>
      <div class="btns">
        <div v-show="!form.view" @click="doSubmit" class="btn-submit btn-audit mr15">提交</div>
        <div @click="dialog = false" class="btn-clear">{{form.view ? '返回': '取消'}}</div>
      </div>
    </el-dialog>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        nav: [{name: "学生信息审核", path: ""}],
        searchData: [
          {type: 'select', title: '审核状态', model: 'auditStatus', option: this.$enums.AuditStatus.list},
          {type: 'input', placeholder: '姓名'}
        ],
        table:{
          url: '/student/queryStudentChangeRecordPage.do',
          el: '.el-table',
          total: 0,
          noData: true,
          list: [],
          query: {
            auditStatus: null,
            keywords: ''
          }
        },
        dialog: false,
        form: {
          auditStatus: 'PASS',
          remark: '',
          changeIdList: []
        },

      }
    },
    methods: {
      // 展示弹窗
      async showDialog(item,type){
        this.dialog = true
        let res = await this.$ajax({
          url: '/student/getStudentChangeRecord.do',
          query:{
            id: item.id
          }
        })
        if(type === 'edit'){
          this.form = {
            view: false,
            id: item.id,
            auditStatus: 'PASS',
            remark: '',
            studentChangeList: res.studentChangeList,
            changeIdList: []
          }
        }else{
          this.form = {
            view: true,
            id: item.id,
            auditStatus: item.auditStatus,
            remark: item.remark,
            studentChangeList: res.studentChangeList,
            changeIdList: res.studentChangeList.map(e=> e.changeType)
          }
        }
        console.log(this.form.view)
      },
      // 获取详情
      async getDetail(id){

      },
      // 提交
      async doSubmit(){
        if(!this.form.auditStatus){
          this.$message.warning('请选择审核状态')
          return
        }
        if(this.form.auditStatus === 'PART_PASS' && !this.form.remark){
          this.$message.warning('请填写备注')
          return
        }
        if(this.form.auditStatus === 'NOT_PASS' && !this.form.remark){
          this.$message.warning('请填未通过原因')
          return
        }

        let query = JSON.parse(JSON.stringify(this.form))
        if(query.auditStatus === 'PASS'){
          query.changeIdList = undefined
          query.remark = undefined
          query.studentChangeList = undefined
        }else if(query.auditStatus === 'NOT_PASS'){
          query.changeIdList = undefined
          query.studentChangeList = undefined
        }
        await this.$ajax({
          url: '/student/auditStudentChange.do',
          el: '.btn-audit',
          noText: false,
          query
        })
        this.$remind('审核成功')
        this.dialog = false
        this.$search(this.table)
      }
    },
    mounted(){
      this.$search(this.table)
    }
  }
</script>
